"use client";

import { useState } from "react";

/* ===========================
   CATEGORY CONSTANTS
=========================== */
const INCOME_CATEGORIES = [
  "IRA",
  "Barangay Fees",
  "Rentals",
  "Donations",
  "Others",
];

const EXPENSE_CATEGORIES = [
  "Utilities",
  "Supplies",
  "Honorarium",
  "Maintenance",
  "Projects",
  "Others",
];

export default function TransactionModal({ onClose, onSubmit }) {
  const [type, setType] = useState("income"); // income | expense

  const [form, setForm] = useState({
    date: "",
    description: "",
    category: "",
    deposit: "",
    withdrawal: "",
  });

  const handleSubmit = (e) => {
    e.preventDefault();

    if (!form.category) {
      alert("Please select a category.");
      return;
    }

    const newTx = {
      date: form.date,
      description: form.description,
      category: form.category,
      deposit: type === "income" ? Number(form.deposit) || 0 : 0,
      withdrawal: type === "expense" ? Number(form.withdrawal) || 0 : 0,
    };

    onSubmit(newTx);
    onClose();
  };

  return (
    <div className="fixed inset-0 bg-black/40 flex items-center justify-center z-50">
      <div className="bg-white rounded-xl shadow-xl w-full max-w-md p-6">

        {/* HEADER */}
        <h2 className="text-xl font-semibold mb-6 text-slate-800">
          Add Transaction
        </h2>

        <form className="space-y-4" onSubmit={handleSubmit}>

          {/* ================= TYPE FILTER ================= */}
          <div>
            <label className="block text-sm font-medium mb-2">
              Transaction Type
            </label>
            <div className="flex gap-3">
              <button
                type="button"
                onClick={() => {
                  setType("income");
                  setForm({ ...form, category: "", withdrawal: "" });
                }}
                className={`flex-1 px-4 py-2 rounded-lg border text-sm font-medium
                  ${type === "income"
                    ? "bg-emerald-600 text-white border-emerald-600"
                    : "bg-white text-slate-700 hover:bg-slate-50"}`}
              >
                Income
              </button>

              <button
                type="button"
                onClick={() => {
                  setType("expense");
                  setForm({ ...form, category: "", deposit: "" });
                }}
                className={`flex-1 px-4 py-2 rounded-lg border text-sm font-medium
                  ${type === "expense"
                    ? "bg-rose-600 text-white border-rose-600"
                    : "bg-white text-slate-700 hover:bg-slate-50"}`}
              >
                Expense
              </button>
            </div>
          </div>

          {/* ================= DATE ================= */}
          <div>
            <label className="block text-sm font-medium mb-1">Date</label>
            <input
              type="date"
              required
              value={form.date}
              onChange={(e) => setForm({ ...form, date: e.target.value })}
              className="w-full border rounded-lg px-3 py-2"
            />
          </div>

          {/* ================= DESCRIPTION ================= */}
          <div>
            <label className="block text-sm font-medium mb-1">Description</label>
            <input
              type="text"
              required
              value={form.description}
              onChange={(e) =>
                setForm({ ...form, description: e.target.value })
              }
              className="w-full border rounded-lg px-3 py-2"
            />
          </div>

          {/* ================= CATEGORY FILTER ================= */}
          <div>
            <label className="block text-sm font-medium mb-1">
              {type === "income" ? "Income Category" : "Expense Category"}
            </label>
            <select
              required
              value={form.category}
              onChange={(e) =>
                setForm({ ...form, category: e.target.value })
              }
              className="w-full border rounded-lg px-3 py-2 bg-white"
            >
              <option value="">Select category</option>
              {(type === "income"
                ? INCOME_CATEGORIES
                : EXPENSE_CATEGORIES
              ).map((cat) => (
                <option key={cat} value={cat}>
                  {cat}
                </option>
              ))}
            </select>
          </div>

          {/* ================= AMOUNT ================= */}
          {type === "income" && (
            <div>
              <label className="block text-sm font-medium mb-1">
                Deposit Amount (‚Ç±)
              </label>
              <input
                type="number"
                required
                value={form.deposit}
                onChange={(e) =>
                  setForm({ ...form, deposit: e.target.value })
                }
                className="w-full border rounded-lg px-3 py-2"
              />
            </div>
          )}

          {type === "expense" && (
            <div>
              <label className="block text-sm font-medium mb-1">
                Withdrawal Amount (‚Ç±)
              </label>
              <input
                type="number"
                required
                value={form.withdrawal}
                onChange={(e) =>
                  setForm({ ...form, withdrawal: e.target.value })
                }
                className="w-full border rounded-lg px-3 py-2"
              />
            </div>
          )}

          {/* ================= ACTIONS ================= */}
          <div className="flex justify-end gap-3 pt-4">
            <button
              type="button"
              onClick={onClose}
              className="px-4 py-2 rounded-lg border text-slate-600 hover:bg-slate-100"
            >
              Cancel
            </button>

            <button
              type="submit"
              className="px-4 py-2 rounded-lg bg-slate-800 text-white font-medium hover:bg-slate-900"
            >
              Save Transaction
            </button>
          </div>

        </form>
      </div>
    </div>
  );
}
